.emailList{
    flex:1;
    overflow: scroll;
}

.emailList_settings{
    position: sticky;
    top: 0;
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid whitesmoke;
    background-color: white;
    z-index:999;
    padding-right: 10px;
}

.emaliList_sections{
position: sticky;
top:0;
display: flex;
border-bottom: 1px solid whitesmoke;
background-color: white;
z-index: 999;
}

.emailList_list{
    padding-bottom: 20%;
}

.emailList::-webkit-scrollbar {
    display: none;
}

.emailList{
    -ms-overflow-style: none;
    scrollbar-width: none;
}